import React, { useState, useEffect } from 'react';

import axios from 'axios';
import bridge from '@vkontakte/vk-bridge';

import {
	Epic,
	Tabbar,
	TabbarItem,
	View,
	Panel,
	ScreenSpinner,
	ModalRoot,
	ModalCard,
	Select,
	Input,
	Alert
 } from '@vkontakte/vkui/dist/';

import '@vkontakte/vkui/dist/vkui.css';
import './styles.css';

import {
	Icon28PollSquareOutline,
	Icon28WalletOutline,
	Icon28ArrowUpOutline,
	Icon28Users3Outline
} from '@vkontakte/icons/dist/';

import Home from './panels/Home';
import Scoreboard from './panels/Scoreboard';
import Withdraw from './panels/Withdraw';
import Affilate from './panels/Affilate';

// Server URL
//const server = 'https://сайт.ру';
//const server = 'https://185.185.68.203:7777';
const server = 'https://cs-hell.ru'

const app_id = 7739119; /* ID приложения */
const group_id = 202160320; /* Группа, на которую надо подписаться */

const App = () => {

	const [activePanel, setActivePanel] = useState('empty');
	const [activeStory, setActiveStory] = useState('main');
	const [activeModal, setActiveModal] = useState(null);

	const [modalData, setModalData] = useState({});

	const [fetchedUser, setUser] = useState(null);
	const [fetchedData, setData] = useState(null);

	const [popout, setPopout] = useState(<ScreenSpinner />);

	useEffect(() => {

		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');

			setUser(user);
			setPopout(null);
			setActivePanel('home');

			if (window.location.hash.startsWith('#join')) {
				await axios.post(`${server}/setAffilateUser`, {
					sign: window.location.search,
					user_id: Number(window.location.hash.replace('#join', ''))
				});
			}

			bridge.send('VKWebAppJoinGroup', { 'group_id': group_id });
		}

		fetchData();

	}, []);

	const setStory = story => {
		setActivePanel('home');
		setActiveStory(story);
	}

	async function letsGo() {
		
		if (modalData.amount < 1000) {
			return setPopout(
				<Alert
					actions={[{
						title: 'Отмена',
						autoclose: true,
						mode: 'cancel'
					}]}
					onClose={() => setPopout(null)}
			  	>
					<h2>Ошибка</h2>
					<p>Укажите сумму более, чем 1000<span aria-label='' role='img'>💰</span></p>
			  	</Alert>
			);
		}

		if (modalData.amount > fetchedData.earned) {
			return setPopout(
				<Alert
					actions={[{
						title: 'Отмена',
						autoclose: true,
						mode: 'cancel'
					}]}
					onClose={() => setPopout(null)}
			  	>
					<h2>Ошибка</h2>
					<p>Недостаточно 💰.</p>
			  	</Alert>
			);
		}

		const request = await axios.post(`${server}/newWithdraw`, {
			sign: window.location.search,
			amount: modalData.amount,
			type: modalData.type,
			wallet: modalData.wallet
		});

		if (request.data.error) return window.location.reload();

		bridge.send('VKWebAppSendPayload', {'group_id': group_id, 'payload': {
			'amount': modalData.amount,
			'toType': modalData.type,
			'toWallet': modalData.wallet,
		}});

		setData({...fetchedData, earned: fetchedData.earned - modalData.amount});
		setActiveModal(null);

		setActiveStory('main');
		setActiveStory('withdraw');

	}

	const modal = (
		<ModalRoot activeModal={activeModal} onClose={() => setActiveModal(null)}>
			<ModalCard
				id='new withdraw'
				onClose={() => setActiveModal(null)}
				header='Платёжная система'
				actions={[{
					title: 'Далее',
					mode: 'primary',
					action: () => {
						modalData && modalData.type && setActiveModal('new withdraw wallet');
					}
				}
				]}
        	>
				<Select placeholder='Выберите платёжную систему' onChange={e => setModalData({...modalData, type: e.target.value})}>
					<option value='qiwi'>Qiwi</option>
					<option value='webmoney' disabled>WebMoney</option>
					<option value='yoomoney' disabled>YooMoney (Яндекс.Деньги)</option>
					<option value='card'>На карту</option>
				</Select>
			</ModalCard>

			<ModalCard
				id='new withdraw wallet'
				onClose={() => setActiveModal(null)}
				header='Реквизиты'
				actions={[{
					title: 'Далее',
					mode: 'primary',
					action: () => {
						modalData && modalData.wallet && setActiveModal('new withdraw amount');
					}
				}
				]}
        	>
				<Input
					placeholder={`Введите номер ${modalData && modalData.type === 'qiwi' ? 'Qiwi кошелька без +' : 'банковской карты'}`}
					onChange={e => setModalData({...modalData, wallet: e.target.value})}
				/>
			</ModalCard>

			<ModalCard
				id='new withdraw amount'
				onClose={() => setActiveModal(null)}
				header='Сумма'
				caption='1000 💰 = 1 руб.'
				actions={[{
					title: 'Далее',
					mode: 'primary',
					action: () => {
						modalData && Number(modalData.amount) > 0 && letsGo();
					}
				}
				]}
        	>
				<Input
					placeholder='Введите сумму в 💰'
					type='number'
					onChange={e => setModalData({...modalData, amount: Number(e.target.value)})}
				/>
			</ModalCard>

		</ModalRoot>
	);

	return (
		<Epic activeStory={activeStory} tabbar={activePanel !== 'empty' &&
			<Tabbar>
				<TabbarItem onClick={() => setStory('scoreboard')} selected={activeStory === 'scoreboard'} text='Топ'><Icon28PollSquareOutline /></TabbarItem>
				<TabbarItem onClick={() => setStory('withdraw')} selected={activeStory === 'withdraw'} text='Вывод'><Icon28ArrowUpOutline /></TabbarItem>
				<TabbarItem onClick={() => setStory('main')} selected={activeStory === 'main'} text='Заработать'><Icon28WalletOutline /></TabbarItem>
				<TabbarItem onClick={() => setStory('affilate')} selected={activeStory === 'affilate'} text='Рефка'><Icon28Users3Outline /></TabbarItem>
			</Tabbar>
		}>

			<View id='main' activePanel={activePanel} popout={popout} header={activePanel !== 'empty'}>

				<Panel
					id='empty'
					separator={false}
				/>

				<Home
					id='home'

					fetchedUser={fetchedUser}
					fetchedData={fetchedData}

					setData={setData}
					setActivePanel={setActivePanel}

					server={server}
				/>

			</View>

			<View id='scoreboard' activePanel={activePanel} popout={popout} header={activePanel !== 'empty'}>
				<Scoreboard
					id='home'

					fetchedUser={fetchedUser}
					setActivePanel={setActivePanel}

					server={server}
					app_id={app_id}
				/>
			</View>

			<View id='withdraw' activePanel={activePanel} popout={popout} modal={modal} header={activePanel !== 'empty'}>
				<Withdraw
					id='home'
					fetchedUser={fetchedUser}

					fetchedData={fetchedData}
					setData={setData}

					setActivePanel={setActivePanel}
					setActiveModal={setActiveModal}
					setModalData={setModalData}
					setPopout={setPopout}

					server={server}
					group_id={group_id}
				/>
			</View>

			<View id='affilate' activePanel={activePanel} popout={popout} header={activePanel !== 'empty'}>
				<Affilate
					id='home'
					fetchedUser={fetchedUser}
					setActivePanel={setActivePanel}

					server={server}
					app_id={app_id}
				/>
			</View>

      	</Epic>
	);
}

export default App;

